import Vue from 'vue'
import VueRouter from 'vue-router'
import Splashscreen from '@/components/Splashscreen'
import Home from '@/components/Home'
import Index from '@/components/Index'
import FoodItem from '@/components/FoodItem'
import Fastfood from '@/components/categories/Fastfood'
import Drinks from '@/components/categories/Drinks'
import Snack from '@/components/categories/Snack'
import Swallow from '@/components/categories/Swallow'

Vue.use(VueRouter)

export default new VueRouter({
    routes: [
        {
            path: '/',
            name: 'Splashscreen',
            component: Splashscreen
        },
        {
            path: '/home',
            name: 'Hello',
            component: Home
        },
        {
            path: '/index',
            name: 'Index',
            component: Index
        },
        {
            path: '/home/:id',
            name: 'FoodItem',
            props: true,
            component: FoodItem
        },
        {
            path: '/categories/fastfood',
            name: 'Fastfood',
            component: Fastfood
        },
        {
            path: '/categories/swallow',
            name: 'Swallow',
            component: Swallow
        },
        {
            path: '/categories/snack',
            name: 'Snack',
            component: Snack
        },
        {
            path: '/categories/drinks',
            name: 'Drinks',
            component: Drinks
        }
    ],

    mode: 'history'
})