<template>
    <v-app>
        <v-toolbar>
            <v-toolbar-side-icon></v-toolbar-side-icon>
            <v-toolbar-title>
                Food App
            </v-toolbar-title>
        </v-toolbar>
        <v-container grid-list-xs>
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card>
                        <v-img :src="foodItem.imageUrl" height="100%">
                        </v-img>
                        <v-card-title primary-title>
                            <div>
                                <h3 class="headline mb-0">{{foodItem.name}}</h3>
                                <div>{{foodItem.category}}</div>
                            </div>
                            <v-spacer></v-spacer>
                            <v-btn flat color="primary">BUY</v-btn>
                        </v-card-title>
                    </v-card>
                </v-flex>
            </v-layout>
        </v-container>

        <v-container grid-list-xs>
            <v-layout row wrap>
                <v-flex xs12>
                    <h1>Description</h1>
                    <div>
                        {{foodItem.description}}
                    </div>
                </v-flex>
            </v-layout>
        </v-container>
    </v-app>
</template>

<script>
export default {
    props: ['id'],
    computed: {
        foodItem () {
            return this.$store.getters.loadFoodList(this.id)
        }
    }
}
</script>